body {
  background-color: white;
}
.App{
  background-color: cornflowerblue;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
  border-radius: 10px;

}
#to-do-form input {
  background-color: azure;
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: black;
}
#to-do-form input::placeholder{
  color: lightsteelblue;
}
#to-do-form input:focus{
  outline: none;
}
#to-do-form button{
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color: yellowgreen;
  cursor: pointer;
  font-weight: bolder;
  font-size: 18px;
  color: darkslategray;
}
